import React from "react";
import LoginForm from "app/views/common/forms/Login";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import { Redirect } from "react-router-dom";
import clsx from "clsx";
import "./login.scss";

function LoginComponent(props) {
  return props.auth.isAuthenticated ? (
    <Redirect push to={{ pathname: `/courses` }} />
  ) : (
    <div id="cfg_login_page" className="page">
      <Row
        className={clsx(
          "h-100",
          "w-100",
          "justify-content-center",
          "align-items-center"
        )}
      >
        <Col xs={10} md={8} lg={6}>
          <LoginForm handleSubmit={props.handleSubmit} auth={props.auth} />
        </Col>
      </Row>
    </div>
  );
}

export default LoginComponent;
